@media (max-width: 576px) {
  .main-header {
    &__contact-btn-wrap {
      display: none;
    }
  }

  .section {
    &__title {
      font-size: 1.5rem;
      line-height: 1.8rem;
      text-align: left;
    }
    &__header {
      .section__header-details-btn {
        font-size: 12px;
        margin-right: 15px;
        position: static;
        flex: 0 0 auto;
        position: static;
      }

      .section__title {
        margin: unset;
      }
    }
  }

  .contact-form {
    padding: 1.25rem 2rem;
  }
}
